body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  margin-top: 20px;
  margin-left: 5%;
  margin-right: 5%;
}
.App-header {
  margin-left: 15%;
  margin-right: 15%;
}
/* channel listing and search button */
.channel-list {
  list-style: none;
  font-weight: bold;
}
.searchButton {
  width: 80%;
  padding-left: 10%;
}
/* CSS for video listing and video display */
.row-videos {
  width: 100%;
  margin-top: 2%;
}
.video-list {
  width: 50%;
  margin: 1%;
  float: left;
}
.video-list-item {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 2%;
  font-size: 14px;
  text-align: justify;
  cursor: pointer;
}
.video-list-item:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.hide-list {
  color: white;
  float: right;
  margin-right: 20px;
  font-size: 16px;
  padding: 1%;
  background-color: rgb(17, 17, 94);
}
.hide-list:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.video-frame {
  width: 40%;
  margin: 1% 2%;
  position: relative;
  overflow: hidden;
  padding-top: 66.66%;
  float: right;
}

.responsive-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 40%;
  border: none;
}

#errorDiv {
  display: none;
  margin: 5% 15%;
  font-weight: bold;
  color: red;
}

/* Mobile responsive CSS */
@media only screen and (max-width: 500px) {
  .App-header {
    margin: 1%;
    font-size: small;
    font-weight: bold;
  }

  .hide-list {
    font-size: 14px;
  }
  .row-videos {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .row-videos .video-list:nth-child(1) {
    order: 2;
    width: 100%;
    font-size: 14px;
    margin: 1%;
  }
  .row-videos .video-frame:nth-child(2) {
    order: 1;
    width: 100%;
    margin: 2% 0%;
  }
  .responsive-iframe {
    height: 100%;
  }
  #errorDiv {
    margin: 2% 1%;
    text-align: justify;
  }
}
